import React from 'react'
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Link } from 'react-router-dom';
import Styled from './styles'
import logo from '../../../assets/images/logopng.png'

declare module '@mui/material/AppBar' {
    interface AppBarPropsColorOverrides {
      neutral: true;
    }
  }

interface Props {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window?: () => Window;
  }

const drawerWidth = 240;

interface Navigation {
  name: string
  path: string
}

const navItems: Navigation[] = [
  {name: "Accueil", path: `/`},
  {name: " propos", path: `/about`},
  {name: "Admission", path: `/admission`},
  {name: "Actualits", path: `/news`},
  {name: "Contact", path: `/contact`}
];

const NavBar2 = ({ window }: Props) => {

  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const container = window !== undefined ? () => window().document.body : undefined;

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Box sx={{ my: 2, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <Styled.LogoImg
                src={logo}
                alt="al-bader log"
            />
      </Box>
      <Divider />
      <List>
        {navItems.map(({name, path}) => (
          <ListItem key={name} disablePadding>
            <ListItemButton sx={{ textAlign: 'center' }}>
              <Link key={name} to={path} style={{textDecoration: 'none', color: 'inherit', marginLeft: 'auto', marginRight: 'auto'}}><ListItemText primary={name} /></Link>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{width: '100%', height: '90px', position: 'sticky', top: 0, backgroundColor: 'quaternary.main', zIndex: 1}}>
        <Box sx={{maxWidth: '1084px', padding: '0 10px', height: '100%', marginLeft: 'auto', marginRight: 'auto', display: 'flex', alignItems: 'center'}}>
          <AppBar component="nav" position="relative" color="transparent" style={{boxShadow: 'none'}}>
              <Toolbar sx={{padding: { xs: '0px', sm: '0px' }}}>
              
                <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
                  <Link to={"/"} style={{display: 'flex', alignItems: 'center'}}>
                    <Styled.LogoImg
                        src={logo}
                        alt="al-bader log"
                    />
                    <Typography
                    variant="h5"
                    component="div"
                    sx={{ flexGrow: 1, display: { sm: 'block' }, color: 'primary.main', fontSize: {sm: '28px', xs: '20px'} }}
                      >
                          Collge Al-Bader
                      </Typography>
                    </Link>
                  </Box>
                
                <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
                    {navItems.map(({name, path}) => (
                    <Button key={name} sx={{ color: 'primary.main' }}>
                        <Link key={name} to={path} style={{textDecoration: 'none', color: 'inherit'}}>{name}</Link>
                    </Button>
                    ))}
                </Box>
                <IconButton
                    aria-label="open drawer"
                    edge="end"
                    onClick={handleDrawerToggle}
                    sx={{ display: { sm: 'none' }, color: "primary.main" }}
                >
                    <MenuIcon />
                </IconButton>
              </Toolbar>
          </AppBar>
          <Box component="nav">
              <Drawer
              container={container}
              variant="temporary"
              open={mobileOpen}
              anchor="right"
              onClose={handleDrawerToggle}
              ModalProps={{
                  keepMounted: true, // Better open performance on mobile.
              }}
              sx={{
                  display: { xs: 'block', sm: 'none' },
                  '& .MuiDrawer-paper': { boxSizing: 'border-box', width: '100%' },
                  color: 'primary.main'
              }}
              >
              {drawer}
              </Drawer>
          </Box>
        </Box>
    </Box>
  )
}

export default NavBar2
